import React, { Component } from "react";
import "./readQuestion.scss";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { TimerComponent } from "./timerComponent";
import * as Actions from "../../store/User/UserAction";
import { bindActionCreators } from "redux";

export class ReadQuestion extends Component {
  constructor(props) {
    super(props);

    this.state = {};
  }
  componentDidMount() {
    if (
      this.props &&
      this.props.responseId &&
      this.props.type &&
      this.props.question
    ) {
      const payload = {
        questionId: this.props.question.id,
        responseId: this.props.responseId,
        type: this.props.type,
        questionStatus: 2,
      };
      this.props.UserAction.updateResponse(payload);
    }
  }

  remainingTimeHandler = (remainingTime) => {
    return remainingTime;
  };

  render() {
    // console.log(this.props.question,this.props.quesNo)
    return (
      <div className='read-question-main'>
        <div className='read-question-heading'>Live Interview</div>
        <div className='read-question-body'>
          <div className='read-question-timer'>
            <TimerComponent
              durationTime={this.props.durationTime}
              className='timer-new'
              nextBtn={true}
              handleTimer={this.props.readQuestionTimerEnds}
              remainingTimeHandler={this.remainingTimeHandler}
              isPlaying={true}
            />
          </div>
          <div className='read-question-time-left'>
            Time left to read the question
          </div>
          <div className='read-question-ques-div'>
            <div className='read-question-time-left'>
              Please Read Question / Prepare Answer
            </div>
            <div className='ques-wrapper'>
              <div className='question-number'>Q.{this.props.quesNo} </div>
              <div
                className='question-text-new'
                dangerouslySetInnerHTML={{
                  __html:
                    this.props.question && this.props.question.QuestionText,
                }}
              ></div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    UserReducer: state.UserReducer,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    UserAction: bindActionCreators(Actions, dispatch),
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(ReadQuestion));
