import React, { Component } from "react";
import Typography from "@material-ui/core/Typography";
import "./previewInstructionsVerbal.scss";
import birballogo from "../../assets/images/login_page_logo.png";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import { secondsToHours } from "../../utils/utility";
import Button from "@material-ui/core/Button";
import { bindActionCreators } from "redux";
import * as Actions from "../../store/User/UserAction";
import { connect } from "react-redux";

export class Instructions extends Component {
  constructor(props) {
    super(props);

    this.state = {
      count: 0,
      totalTime: "",
      responseId: localStorage.getItem("responseId"),
    };
  }

  componentDidMount() {
    // if (this.props.totalCount && this.props.duration) {
    this.setState({
      count: this.props.totalCount,
      totalTime: this.props.duration,
    });

    this.state.responseIdresponseId &&
      this.props.UserAction.addEvent({
        responseId: this.state.responseId,
        event: {
          type: "debug",
          level: 3,
          metadata: { Page: "Verbal Introduction Page" },
          timestamp: new Date().toISOString(),
        },
      });
  }

  render() {
    return (
      <div className="instruction-main-div">
        <div className="main-wrapper">
          <div className="left-content">
            <div className="instructions-heading">
              <Typography>Instructions</Typography>
            </div>
            <div className="instructions-subHeading">
              <Typography>Read and follow the given Instructions</Typography>
            </div>
          </div>

          <div className="right-content">
            <div className="instructions-logo">
              <img alt="" src={birballogo}></img>
            </div>
          </div>
        </div>

        <div className="page-content">
          <div className="contents-verbal">
            <div>
              <div className="li-2">
                <div className="dot"></div> Question Type : Verbal
              </div>
              <div className="li-2">
                <div className="dot"></div>Total No. of Questions :{" "}
                {this.state.count}
              </div>
              <div className="li-2">
                <div className="dot"></div> Total Time :{" "}
                {secondsToHours(this.state.totalTime)}
              </div>
            </div>
          </div>
        </div>
        <div className="continue-button-wrapper">
          <Button className="continue-button" onClick={this.props.continue}>
            Continue
          </Button>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  UserReducer: state.UserReducer,
});

function mapDispatchToProps(dispatch) {
  return {
    UserAction: bindActionCreators(Actions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Instructions);
