import React from "react";
import { Interview } from "../../utils/LazyLoaded";
import interview from "./interview";
import Submitting from "./submitting";
import { withRouter } from "react-router-dom";
import { bindActionCreators } from "redux";
import * as Actions from "../../store/User/UserAction";
import { connect } from "react-redux";
import { store } from "react-notifications-component";
import {serverAddress} from "../../config";
import ReactGA from "react-ga";
import McqInteview from "./mcqQuestions";
import VerbalSummary from "./PreviewInstructionsVerbal";
import McqSummary from "./PreviewInstructionsMCQ";
import CodingSummary from "./PreviewInstructionsCoding";
import Dialog from "@material-ui/core/Dialog";
import warning from "../../assets/images/warning.svg";
import "./parent.scss";
import CircularProgress from "@material-ui/core/CircularProgress";
import CodingInterview from "./CodeEditor";

let indexedDB =
    window.indexedDB ||
    window.webkitIndexedDB ||
    window.mozIndexedDB ||
    window.OIndexedDB ||
    window.msIndexedDB,
  IDBTransaction =
    window.IDBTransaction ||
    window.webkitIDBTransaction ||
    window.OIDBTransaction ||
    window.msIDBTransaction,
  dbVersion = 2;

let request = indexedDB.open("elephantFiles", dbVersion);

request.onsuccess = function (event) {
  let db = event.target.result;

  db.onerror = function (event) {
    console.log("Error creating/accessing IndexedDB database");
  };
};
request.onupgradeneeded = function (event) {
  let db = event.target.result;
  let objectStore = db.createObjectStore("videoAnswers", {
    keyPath: "questionId",
  });
};

let reTrial = 3;
class Parent extends React.Component {
  state = {
    submitClicked: false,
    submittedQuestions: [],
    totalQuestions: 0,
    progress: 0,
    submissionInProgress: false,
    submissionQueue: [],
    // currentQuestion: "",
    data: null,
    totalMcq: 0,
    totalVerbal: 0,
    totalCoding: 0,
    mcqQuestions: [],
    sequenceOrder: "",
    verbalQuestions: [],
    verbalNew: false,
    mcqNew: false,
    codingNew: false,
    suggestedVerbalDuration: 0,
    suggestedCodingDuration: 0,
    showDialog: false,
    order: [],
  };

  componentDidMount() {
    window.history.pushState(null, "", window.location.href);
    window.onpopstate = function () {
      window.history.pushState(null, "", window.location.href);
    };
    window.addEventListener("beforeunload", this.keepOnPage);
    ReactGA.pageview(window.location.pathname);
    const stateData =
      this.props.history &&
      this.props.history.location &&
      this.props.history.location.state;
    const totalMcq = stateData && stateData.totalMcq;
    const totalVerbal = stateData && stateData.totalVerbal;
    const totalCoding = stateData && stateData.totalCoding;
    const newResponse =
      stateData && stateData.data && stateData.data.newResponse;
    let verbalCount = 0;
    let mcqCount = 0;
    let codingCount = 0;
    let verbalDuration = 0,
      codingDuration = 0,
      mcqDuration = 0;
    if (stateData) {
      const { response } = stateData && stateData.data;
      const { verbalQuestions, mcqQuestions, codingQuestions } = stateData;
      verbalCount =
        response && response.verbalQuestions && response.verbalQuestions.length;
      mcqCount =
        response && response.mcqQuestions && response.mcqQuestions.length;
      codingCount =
        response && response.codingQuestions && response.codingQuestions.length;
      verbalQuestions &&
        verbalQuestions.length > 0 &&
        verbalQuestions.map((el) => {
          verbalDuration += el.suggestedDuration;
        });

      mcqQuestions &&
        mcqQuestions.length > 0 &&
        mcqQuestions.map((el) => {
          mcqDuration += el.suggestedDuration;
        });
      codingQuestions &&
        codingQuestions.length > 0 &&
        codingQuestions.map((el) => {
          codingDuration += el.suggestedDuration;
        });
    }
    this.setState({
      ...stateData,
      totalQuestions: totalMcq + totalVerbal + totalCoding,
      verbalNew: newResponse
        ? true
        : verbalCount === totalVerbal
        ? true
        : false,
      mcqNew: newResponse ? true : mcqCount === totalMcq ? true : false,
      codingNew: newResponse
        ? true
        : codingCount === totalCoding
        ? true
        : false,
      suggestedMcqDuration: mcqDuration,
      suggestedVerbalDuration: verbalDuration,
      suggestedCodingDuration: codingDuration,
      order: stateData && stateData.order,
    });
  }

  componentWillUnmount() {
    window.removeEventListener("beforeunload", this.keepOnPage);
  }

  keepOnPage(e) {
    var message =
      "Warning!\n\nNavigating away from this page will clear your response.";
    e.returnValue = message;
    return message;
  }

  submitClickedHandler = () => {
    this.setState({
      submitClicked: true,
      progress: 0,
    });
  };

  totalQuestionsHandler = (val) => {
    this.setState({
      totalQuestions: val,
    });
  };

  addToQueue = (body, quesData) => {
    let submissionQueue = this.state.submissionQueue;
    submissionQueue.push(body);

    this.setState({
      submissionQueue: submissionQueue,
      // currentQuestion: quesData.quesNo,
    });

    this.callSubmitApi();
  };

  callSubmitApi = () => {
    let _this = this;
    let submissionQueue = this.state.submissionQueue;

    if (
      submissionQueue.length > 0 &&
      this.state.submissionInProgress == false
    ) {
      console.log('insidee')
      this.setState({
        submissionInProgress: true,
      });
      let body = submissionQueue.shift();
      _this.setState({
        submissionQueue: submissionQueue,
      });
      reTrial -= 1;
      let formdata = body.formdata;
      let payload = body.payload;

      localStorage.setItem("questionId", body.payload.questionId);
      localStorage.setItem("questionStatus", body.payload.questionStatus);
      localStorage.setItem("type", body.payload.type);
      var ajax = new XMLHttpRequest();
      ajax.timeout = 1000 * 60 * 10; // time in milliseconds
      ajax.upload.addEventListener(
        "progress",
        function progressHandler(event) {
          var percent = (event.loaded / event.total) * 100;
          _this.props.UserAction.addsingleanswerProgress({ progress: percent });
        },
        false
      );
      ajax.addEventListener(
        "load",
        async function completeHandler(event) {
          console.log('complete Handler')
          if (event.target.status == 200) {
            _this.props.UserAction.addsingleanswerSuccess(
              JSON.parse(event.target.responseText)
            );
            _this.setState({
              submissionInProgress:false
            })
          } else {
            if (reTrial) {
              let submissionQueue = _this.state.submissionQueue;
              submissionQueue.unshift(body);
              await _this.setState({
                submissionQueue: submissionQueue,
                submissionInProgress: false,
                showDialog: true,
              });
              _this.callSubmitApi();
            } else {
              _this.props.UserAction.addsingleanswerError({
                singleAnswerError: true,
              });

              setTimeout(() => {
                _this.setState({
                  showDialog: false,
                });
              }, 2000);
            }
          }

          _this.setState({
            progress: 0,
          });
          _this.callSubmitApi();
        },
        false
      );

      ajax.addEventListener(
        "error",
        async function errorHandler(event) {
          console.log('error Handler')
          if (reTrial) {
            let submissionQueue = _this.state.submissionQueue;
            submissionQueue.unshift(body);
            await _this.setState({
              submissionQueue: submissionQueue,
              submissionInProgress: false,
              showDialog: true,
            });
            _this.callSubmitApi();
          } else {
            _this.props.UserAction.addsingleanswerError({
              singleAnswerError: true,
            });
            setTimeout(() => {
              _this.setState({
                showDialog: false,
              });
            }, 2000);
          }

          // store.addNotification({
          //   title: "Error",
          //   message: "Error Occured!",
          //   type: "danger",
          //   container: "top-center",
          //   animationIn: ["animated", "fadeIn"],
          //   animationOut: ["animated", "fadeOut"],
          //   dismiss: {
          //     duration: 2000,
          //   },
          // });
        },
        false
      );
      ajax.addEventListener(
        "abort",
        function abortHandler(event) {
          // _("status").innerHTML = "Upload Aborted";
        },
        false
      );
      ajax.open(
        "PATCH",
        `${serverAddress}/response/submission?responseId=${payload.responseId}&questionId=${payload.questionId}&questionStatus=${payload.questionStatus}&type=${payload.type}`
      );
      ajax.setRequestHeader("Authorization", localStorage.getItem("tokenNew"));
      ajax.send(formdata);
    } else {
      // this.setState({
      //   submissionInProgress: false,
      // });
    }
  };

  upload = async (formdata, payload, indexDbData, quesNo, questionType) => {
    this.addToQueue(
      {
        formdata: formdata,
        payload: payload,
      },
      { no: quesNo, type: questionType }
    );

    this.decreaseCountHandler(questionType);

    let request = indexedDB.open("elephantFiles", dbVersion);

    request.onsuccess = function (event) {
      let db = event.target.result;
      let transaction = db.transaction(["videoAnswers"], "readwrite");
      transaction
        .objectStore("videoAnswers", { keyPath: "questionId" })
        .add(indexDbData);

      db.onerror = function (event) {
        console.log("Error creating/accessing IndexedDB database");
      };
    };
  };

  static getDerivedStateFromProps(nextProps, currentState) {
    if (
      nextProps &&
      nextProps.UserReducer &&
      nextProps.UserReducer.singleAnswerProgress &&
      !nextProps.UserReducer.isUserquestionDataAdded
    ) {
      currentState.progress = nextProps.UserReducer.singleAnswerProgress;
    }

    if (
      nextProps &&
      nextProps.UserReducer &&
      nextProps.UserReducer.isUserquestionDataAdded &&
      nextProps.UserReducer.singleAnswerData
    ) {
      nextProps.UserReducer.isUserquestionDataAdded = false;
      reTrial = 3;
      currentState.showDialog = false;
      nextProps.UserAction.addEvent({
        responseId:
          currentState &&
          currentState.data &&
          currentState.data.response &&
          currentState.data.response.id,
        event: {
          type: "api",
          level: 1,
          metadata: {
            detail: {
              questionId: localStorage.getItem("questionId"),
              status: localStorage.getItem("questionStatus"),
              type: localStorage.getItem("type"),
            },
            type: "success",
            // response: nextProps.UserReducer.singleAnswerData,
          },
          timestamp: new Date().toISOString(),
        },
      });
      let request = indexedDB.open("elephantFiles", 1);

      request.onsuccess = function (event) {
        let db = event.target.result;
        let transaction = db.transaction(["videoAnswers"], "readwrite");
        transaction
          .objectStore("videoAnswers", { keyPath: "questionId" })
          .delete(`${localStorage.getItem("questionId")}`);

        db.onerror = function (event) {
          console.log("Error creating/accessing IndexedDB database");
        };
      };

      let submittedQuestions = currentState.submittedQuestions;
      submittedQuestions.push(localStorage.getItem('questionId'));
      currentState.submittedQuestions = submittedQuestions;
      if (
        currentState.submittedQuestions.length < currentState.totalQuestions
      ) {
        currentState.progress = 0;
      }

      // store.addNotification({
      //   title: "Success",
      //   message: `Question ${submittedQuestions.length} Submitted successfully`,
      //   type: "success",
      //   container: "top-center",
      //   animationIn: ["animated", "fadeIn"],
      //   animationOut: ["animated", "fadeOut"],
      //   dismiss: {
      //     duration: 2000,
      //   },
      // });
      if (
        currentState.submittedQuestions.length == currentState.totalQuestions
      ) {
        const payload = {
          responseId:
            currentState.data &&
            currentState.data.response &&
            currentState.data.response.id,
          formdata: { status: "FINISHED" },
        };
        nextProps.UserAction.updateResponse(payload);
        nextProps.UserAction.addEvent({
          responseId:
            currentState &&
            currentState.data &&
            currentState.data.response &&
            currentState.data.response.id,
          event: {
            type: "debug",
            level: 3,
            metadata: {
              Page: "Congratulation",
            },
            timestamp: new Date().toISOString(),
          },
        });
        setTimeout(() => {
          nextProps.history.push({
            pathname: "/congratulation",
          });
        }, 200);
      }
    }

    if (
      nextProps &&
      nextProps.UserReducer &&
      nextProps.UserReducer.singleAnswerError
    ) {
      nextProps.UserReducer.singleAnswerError = false;
      nextProps.UserAction.addEvent({
        responseId:
          currentState &&
          currentState.data &&
          currentState.data.response &&
          currentState.data.response.id,
        isError: true,
        event: {
          type: "api",
          level: 1,
          metadata: {
            detail: {
              questionId: localStorage.getItem("questionId"),
              status: localStorage.getItem("questionStatus"),
              type: localStorage.getItem("type"),
            },
            type: "error",
            response: nextProps.UserReducer.singleAnswerError,
          },
          timestamp: new Date().toISOString(),
        },
      });
      setTimeout(() => {
        nextProps.UserAction.addEvent({
          responseId:
            currentState &&
            currentState.data &&
            currentState.data.response &&
            currentState.data.response.id,
          event: {
            type: "debug",
            level: 3,
            metadata: {
              Page: "Error Page",
            },
            timestamp: new Date().toISOString(),
          },
        });
        nextProps.history.push({
          pathname: "/error",
        });
      }, 2000);
    }

    return currentState;
  }
  changeSequence = (type) => {
    const { order } = this.state;
    const id =
      order && order.length > 0 && order.findIndex((el) => el === type);
    if (id !== -1 || id !== order.length - 1)
      setTimeout(() => {
        this.setState({ sequenceOrder: order[id + 1] });
      }, 2000);
  };
  decreaseCountHandler = (type) => {
    type === "verbal"
      ? this.setState({ totalVerbal: this.state.totalVerbal - 1 })
      : type === "coding"
      ? this.setState({ totalCoding: this.state.totalCoding - 1 })
      : this.setState({ totalMcq: this.state.totalMcq - 1 });
  };

  render() {
    // console.log('dataaaaa',this.state)
    const {
      sequenceOrder,
      totalMcq,
      totalVerbal,
      totalCoding,
      submitClicked,
      totalQuestions,
      submittedQuestions,
      progress,
      verbalNew,
      mcqNew,
      codingNew,
      suggestedMcqDuration,
      suggestedVerbalDuration,
      suggestedCodingDuration,
      order,
    } = this.state;

    return (
      <div className='main-parent'>
        <Dialog
          className='warning-dialog-submit'
          open={this.state.showDialog}
          aria-labelledby='alert-dialog-title'
          aria-describedby='alert-dialog-description'
        >
          <div className='warning-dialog-main'>
            <div className='unstable-image'>
              <img src={warning} />
            </div>
            <div className='warning-dialog-heading'>
              Failed to submit response, Being retried
            </div>
          </div>
        </Dialog>

        {submitClicked && totalMcq < 1 && totalVerbal < 1 && totalCoding < 1 ? (
          <Submitting
            progress={this.state.progress}
            submittedQuestions={this.state.submittedQuestions}
            totalQuestions={this.state.totalQuestions}
            responseId={
              this.state &&
              this.state.data &&
              this.state.data.response &&
              this.state.data.response.id
            }
          />
        ) : verbalNew && sequenceOrder === "verbal" && totalVerbal > 0 ? (
          <VerbalSummary
            totalCount={totalVerbal}
            duration={suggestedVerbalDuration}
            continue={() => this.setState({ verbalNew: false })}
          />
        ) : sequenceOrder === "verbal" && totalVerbal > 0 ? (
          <Interview
            submitClickedHandler={() =>
              totalMcq === 0 && totalCoding === 0
                ? this.submitClickedHandler()
                : this.changeSequence("verbal")
            }
            addToQueue={this.addToQueue}
            totalQuestionsHandler={this.totalQuestionsHandler}
            callSubmitApi={this.callSubmitApi}
            decreaseCountHandler={() => this.decreaseCountHandler("verbal")}
            data={this.state}
            upload={this.upload}
          />
        ) : codingNew && sequenceOrder === "coding" && totalCoding > 0 ? (
          <CodingSummary
            totalCount={totalCoding}
            duration={suggestedCodingDuration}
            continue={() => this.setState({ codingNew: false })}
          />
        ) : sequenceOrder === "coding" && totalCoding > 0 ? (
          <CodingInterview
            submitClickedHandler={() =>
              totalVerbal === 0 && totalMcq === 0
                ? this.submitClickedHandler()
                : this.changeSequence("coding")
            }
            decreaseCountHandler={() => this.decreaseCountHandler("coding")}
            addToQueue={this.addToQueue}
            totalQuestionsHandler={this.totalQuestionsHandler}
            callSubmitApi={this.callSubmitApi}
            data={this.state}
            upload={this.upload}
          />
        ) : mcqNew && sequenceOrder === "mcq" && totalMcq > 0 ? (
          <McqSummary
            totalCount={totalMcq}
            duration={suggestedMcqDuration}
            continue={() => this.setState({ mcqNew: false })}
          />
        ) : sequenceOrder === "mcq" && totalMcq > 0 ? (
          <McqInteview
            submitClickedHandler={() =>
              totalVerbal == 0 && totalCoding === 0
                ? this.submitClickedHandler()
                : this.changeSequence("mcq")
            }
            decreaseCountHandler={() => this.decreaseCountHandler("mcq")}
            addToQueue={this.addToQueue}
            totalQuestionsHandler={this.totalQuestionsHandler}
            callSubmitApi={this.callSubmitApi}
            data={this.state}
            upload={this.upload}
          />
        ) : (
          <div
            style={{
              padding: "20px 0",
              display: "flex",
              justifyContent: "center",
            }}
          >
            <CircularProgress style={{ color: "#04724D" }} />
          </div>
        )}
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  UserReducer: state.UserReducer,
});

function mapDispatchToProps(dispatch) {
  return {
    UserAction: bindActionCreators(Actions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Parent));
