export function secondsToMinutes(time){
    let min=Math.floor(time/60);
    let sec=Math.floor(time % 60);
    if(min<10){
        min=`0${min}`
    }
    if(sec<10){
        sec=`0${sec}`
    }
    return min+':'+sec;
}


export function secondsToHours(time){
    let hours = Math.floor(time / 3600);
time %= 3600;
    let min=Math.floor(time/60);
    let sec=Math.floor(time % 60);
    if(hours<10 && hours!=0){
        hours=`0${hours}`
    }
    if(hours==0){
        hours=`00`
    }
    if(min<10){
        min=`0${min}`
    }
    if(sec<10){
        sec=`0${sec}`
    }
    return hours+':'+min+':'+sec;
}


