import React, { Component } from "react";
import "./mcqQuestions.scss";
import { TimerComponent } from "./timerComponent";
import * as Actions from "../../store/User/UserAction";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { Checkbox } from "@material-ui/core";
import Button from "@material-ui/core/Button";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import WebcamComponent from "./WebcamComponent";
import AudioAnalyser from "../Preview/AudioAnalyser";
import audio from "../../assets/images/audio.png";
import ScreenRecording from "../Test/screenRecording";

let mcqSkipableValue = parseInt(process.env.REACT_APP_MCQ_SKIP_TIME)
  ? parseInt(process.env.REACT_APP_MCQ_SKIP_TIME)
  : 5;

let remainingGlobal = 0,
  durationGlobal = 0;
let Interval = "",
  scheduleIdGlobal = "",
  candidateIdGlobal = "",
  timedate = "",
  activeStep = 0,
  ScreenFileGlobal = "",
  ScreenFileNameGlobal = "";
class McqQuestions extends Component {
  constructor(props) {
    super(props);
    this.clockRef = React.createRef();
    this.mediaRecorderRef = React.createRef();
    this.timer = 0;
    this.state = {
      capturing: false,
      recordedChunks: [],
      videofilename: "",
      submitClicked: false,
      faceNotDetected: 0,
      responseId: "",
      ScheduleId: "",
      CandidateId: "",
      timedate: new Date().toISOString(),
      remainingTime: 0,
      McqData: [],
      totalLength: 0,
      current: 0,
      currentData: null,
      options: null,
      disablebtn: true,
      restartTimer: false,
      isPlaying: true,
      audio: false,
      audioNew: null,
      isMultiCheck: false,
      isSkipped: 0,
      time: "",
      events: [],
      skipableArrived : false
    };
    this.vid = React.createRef();
    this.audio = React.createRef();
    this.webcamRef = React.createRef();
    this.childRef = React.createRef();
  }

  componentDidMount() {
    timedate = this.state.timedate;
    const { questionType, id, candidateId, scheduleId } =
      this.props &&
      this.props.data &&
      this.props.data.data &&
      this.props.data.data.response;
    const isSkipped =
      questionType &&
      questionType.length > 0 &&
      questionType.find((el) => el.name && el.name.toLowerCase() === "mcq");
    const details = this.props && this.props.data;
    const data = details && details.mcqQuestions;
    const responseId = id;
    let multi = 0;
    let options =
      data &&
      data[0] &&
      data[0].metadata &&
      data[0].metadata.option &&
      data[0].metadata.option.length > 0 &&
      data[0].metadata.option.map((el) => {
        multi = el.correct ? multi + 1 : multi;
        return { label: el.text, value: false };
      });

    this.setState({
      McqData: data,
      totalLength: data.length,
      current: 1,
      currentData: data[0],
      options: options,
      responseId: responseId,
      isMultiCheck: multi > 1 ? true : false,
      isSkipped: isSkipped && isSkipped.skipable ? isSkipped.skipable : 0,
      ScheduleId: scheduleId,
      CandidateId: candidateId,
    });
    activeStep = 1;
    scheduleIdGlobal = scheduleId;
    candidateIdGlobal = candidateId;
    let timeOut = isSkipped && isSkipped.skipable ? isSkipped.skipable : 0;
    timeOut = timeOut ? mcqSkipableValue * 1000 : 0;
    if(timeOut){
      this.setState({
        skipableArrived:true
      })
    }
    timeOut &&
      setTimeout(() => {
        this.setState({ disablebtn: false });
      }, timeOut);
    this.getMicrophone();
    document.addEventListener("contextmenu", (e) => {
      e.preventDefault();
      const { events } = this.state;
      const data = {
        type: "browser",
        level: 2,
        metadata: "Right click triggered",
        timestamp: new Date().toISOString(),
      };
      events.push({
        ...data,
        time: durationGlobal - this.state.remainingTime,
      });
      id && this.setState({ events: events });
      id && this.addEventProctoring(id, data);
    });
    console.log(window.location);

    document.addEventListener("visibilitychange", (event) => {
      if (window.location.pathname.includes("/interview-process")) {
        if (document.visibilityState == "visible") {
          const { events } = this.state;
          const data = {
            type: "browser",
            level: 2,
            metadata: "tab is active",
            timestamp: new Date().toISOString(),
          };
          events.push({
            ...data,
            time: durationGlobal - this.state.remainingTime,
          });
          id && this.setState({ events: events });
          id && this.addEventProctoring(id, data);
        } else {
          const { events } = this.state;
          const data = {
            type: "browser",
            level: 2,
            metadata: "tab is inactive",
            timestamp: new Date().toISOString(),
          };
          events.push({
            ...data,
            time: durationGlobal - this.state.remainingTime,
          });
          id && this.setState({ events: events });
          id && this.addEventProctoring(id, data);
        }
      }
    });
  }
  selectionOperation = (type) => {
    const { responseId } = this.state;
    const { events } = this.state;
    const data = {
      type: "browser",
      level: 2,
      metadata: `${type} action triggered`,
      timestamp: new Date().toISOString(),
    };
    events.push({
      ...data,
      time: durationGlobal - this.state.remainingTime,
    });
    responseId && this.setState({ events: events });
    responseId && this.addEventProctoring(responseId, data);
  };

  addEventProctoring = (id, data) => {
    this.props.UserAction.addEvent({
      responseId: id,
      event: data,
    });
  };

  async getMicrophone() {
    const audio = await navigator.mediaDevices.getUserMedia({
      audio: true,
    });
    this.setState({ audioNew: audio });
  }

  changeOption = (e, label) => {
    let { options, isMultiCheck } = this.state;
    options &&
      options.length > 0 &&
      options.map((el, index) => {
        if (isMultiCheck) {
          if (el.label === label) {
            options[index].value = true;
          }
        } else {
          if (el.label === label) {
            options[index].value = true;
          } else {
            options[index].value = false;
          }
        }
      });
    this.setState({ options: options });
  };

  handleStartCaptureClick = () => {
    let isSafari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent);
    this.setState({ events: [] });
    const payload = {
      questionId: this.state.currentData.id,
      responseId: this.state.responseId,
      type: "mcq",
      questionStatus: 2,
    };
    this.props.UserAction.updateResponse(payload);
    if (this.mediaRecorderRef) {
      this.mediaRecorderRef.current = new MediaRecorder(
        this.webcamRef.current.stream,
        {
          mimeType: isSafari
            ? 'video/mp4; codecs="avc1.42E01E, mp4a.40.2"'
            : "video/webm",
          audioBitsPerSecond: 128000,
          videoBitsPerSecond: 625000,
        }
      );
    }
    this.setState(
      {
        capturing: true,
      },
      () => {
        this.childRef.current.startCapture();
      }
    );

    ScreenFileGlobal = "";
    ScreenFileNameGlobal = "";

    if (
      this.mediaRecorderRef &&
      this.mediaRecorderRef.current &&
      this.mediaRecorderRef.current.state == "inactive"
    ) {
      this.mediaRecorderRef.current.start();
      this.mediaRecorderRef.current.addEventListener(
        "dataavailable",
        this.handleDataAvailable
      );
    }
  };

  handleDataAvailable = async ({ data }) => {
    if (data.size > 0) {
      await this.setState({
        recordedChunks: this.state.recordedChunks.concat(data),
      });
      if(this.state.remainingTime<durationGlobal){
        remainingGlobal = durationGlobal - this.state.remainingTime;
      }else{
        remainingGlobal=durationGlobal
      }
      // setTimeout(()=>{
      if (this.state.current === this.state.totalLength) {
        this.handleFinish(this.state.McqData);
      } else {
        this.handleNext(this.state.McqData);
      }
      // },2000)
    }
  };
  handleFinish = async (questiondata) => {
    this.setState({
      submitClicked: true,
    });
    await this.handleDownload(questiondata);
    let quesdata = [];

    questiondata.map((item) => {
      quesdata.push(item.id);
    });
    const question = [];
    question.push(this.getStepContent(this.state.current));

    this.props.submitClickedHandler();
  };

  getStepContent(questiondata, stepIndex) {
    if (questiondata.length > 0) {
      let steparray = [];
      questiondata.map((item) => {
        steparray.push(item.QuestionText);
      });
      const value = steparray[this.state.current];
      switch (this.state.activeStep) {
        case this.state.activeStep:
          return (
            <div>
              <div className='question-text'>
                Q{this.state.activeStep + 1}. {value}
              </div>
            </div>
          );
        default:
          return "Unknown stepIndex";
      }
    }
  }
  handleNext = async (questiondata) => {
    await this.setState({
      restartTimer: true,
      isPlaying: true,
      // remainingTime: remainingGlobal,
    });

    await this.handleDownload(questiondata);
    let quesdata = [];

    questiondata.map((item) => {
      quesdata.push(item.id);
    });
    this.handleStartCaptureClick();
    this.getStepContent(questiondata, this.state.current);
    const { totalLength, current, McqData } = this.state;

    if (current < totalLength && McqData && McqData[current]) {
      let options =
        McqData[current].metadata &&
        McqData[current].metadata.option &&
        McqData[current].metadata.option.length > 0 &&
        McqData[current].metadata.option.map((el) => ({
          label: el.text,
          value: false,
        }));
      this.setState({
        current: current + 1,
        currentData: McqData[current],
        options: options,
        disablebtn: true,
      });

      activeStep = current + 1;
    }
  };
  handleStopCaptureClick = () => {
    if (
      this.mediaRecorderRef &&
      this.mediaRecorderRef.current &&
      this.mediaRecorderRef.current.state == "recording"
    ) {
      this.mediaRecorderRef.current.stop();
      this.setState({
        capturing: false,
      });
    }
  };

  handleDownload = (questiondata) => {
    let quesdata = [];

    questiondata.map((item) => {
      quesdata.push(item.id);
    });
    if (this.state.recordedChunks.length) {
      const blob = new Blob(this.state.recordedChunks, {
        type: "video/webm",
      });
      const url = URL.createObjectURL(blob);
      const a = document.createElement("a");
      document.body.appendChild(a);
      a.style = "display: none";
      a.href = url;
      window.URL.revokeObjectURL(url);
      this.setState({ recordedChunks: [] });
      var file = new File(
        [blob],
        `${this.state.ScheduleId}-${this.state.CandidateId}-${this.state.timedate}-${this.state.current}.mp4`
      );
      var filedata = `${this.state.ScheduleId}-${this.state.CandidateId}-${this.state.timedate}-${this.state.current}.mp4`;

      this.setState({ videofilename: filedata });
      this.handlePriorUpload(
        file,
        `${this.state.ScheduleId}-${this.state.CandidateId}-${this.state.timedate}-${this.state.current}.mp4`,
        quesdata
      );
    }
  };

  handlePriorUpload = async (file, name, quesdata) => {
    if (!this.state.disablebtn) {
      this.setState({
        disablebtn: true,
      });
      let time = mcqSkipableValue * 1000;

      setTimeout(() => {
        this.setState({
          disablebtn: false,
        });
      }, time);
    } else {
      let time = mcqSkipableValue * 1000;

      setTimeout(() => {
        this.setState({
          disablebtn: false,
        });
      }, time);
    }
    const { options } = this.state;
    let chosenOption = "No option Selected";
    options &&
      options.length > 0 &&
      options.map((el) => {
        if (el.value) {
          chosenOption = el.label;
        }
      });
    var formdata = new FormData();
    if (ScreenFileGlobal) {
      formdata.append("video", ScreenFileGlobal, ScreenFileNameGlobal);
    }
    formdata.append("video", file, name);
    formdata.append("timeTaken", remainingGlobal);
    formdata.append("chosenOption", chosenOption);
    formdata.append("events", JSON.stringify(this.state.events));
    const payload = {
      questionId: this.state.currentData.id,
      responseId: this.state.responseId,
      type: "mcq",
      questionStatus: 3,
      chosenOption: chosenOption,
      timeTaken: remainingGlobal,
      // videoFile: file,
      // videoFileName: name,
    };
    let quesNo = this.state.current;
    let questionType = "mcq";
    let indexDbData = {
      questionId: this.state.currentData.id,
      responseId: this.state.responseId,
      type: "mcq",
      questionStatus: 3,
      chosenOption: chosenOption,
      timeTaken: remainingGlobal,
      videoFile: file,
      videoFileName: name,
      screenFile: ScreenFileGlobal,
      screenFileName: ScreenFileNameGlobal,
      events: JSON.stringify(this.state.events),
    };
    this.props.UserAction.addEvent({
      responseId: this.state.responseId,
      event: {
        type: "debug",
        level: 3,
        metadata: {
          type: "Question Submit Start",
          questionId: this.state.currentData.id,
          responseId: this.state.responseId,
          type: "mcq",
          questionStatus: 3,
          chosenOption: chosenOption,
          events: this.state.events,
          timeTaken: remainingGlobal,
        },
        timestamp: new Date().toISOString(),
      },
      questionId: this.state.currentData.id,
      type: "mcq",
      questionStatus: 3,
    });
    this.props.upload(formdata, payload, indexDbData, quesNo, questionType);

    this.setState({
      faceNotDetected: 0,
    });
  };

  handleTimer = async () => {
    // clearInterval(Interval);
    await this.setState({
      restartTimer: false,
      isPlaying: false,
      disablebtn: true,
    });
    setTimeout(() => {
      this.handleStopCaptureClick();
    }, 2000);

    this.childRef.current.stopCapture();
  };

  onScreenAccess() {
    console.log("got Screen access");
  }
  onRecordStart() {
    console.log("started recording");
  }
  onRecordStop() {
    console.log("stopped recording");
  }
  onUserStop() {
    console.log("user stopped screen access");
    ScreenFileGlobal = "";
    ScreenFileNameGlobal = "";
  }
  async onVideoFile(blob) {
    let ScreenFileTemp = new File(
      [blob],
      `$${scheduleIdGlobal}-${candidateIdGlobal}-${timedate}-${activeStep}.mp4`
    );
    let ScreenFileNameTemp = `${scheduleIdGlobal}-${candidateIdGlobal}-${timedate}-${activeStep}Screen.mp4`;

    ScreenFileGlobal = ScreenFileTemp;
    ScreenFileNameGlobal = ScreenFileNameTemp;
  }
  onAbort() {
    console.log("recieved abort callback");
  }
  handleTimeUpdate = async (time, remainingTime) => {
    if (this.state.time != time) {
    await this.setState({ time: time, remainingTime: remainingTime });
    }
  };
  render() {
    const {
      disablebtn,
      current,
      currentData,
      options,
      restartTimer,
      isPlaying,
    } = this.state;

    const duration =
      currentData && currentData.suggestedDuration
        ? currentData.suggestedDuration
        : 0;
    durationGlobal = duration;
    let width = 10;
    options &&
      options.length > 0 &&
      options.map((item) => {
        if (item.label.length * 14 > width) {
          width = item.label.length * 14 + 60;
        }
      });

    return (
      <div
        className='InterViewMcqMain'
        onCopy={() => this.selectionOperation("Copy")}
        onCut={() => this.selectionOperation("Cut")}
        onPaste={() => this.selectionOperation("Paste")}
      >
        <ScreenRecording
          ref={this.childRef}
          onScreenAccess={this.onScreenAccess}
          onUserStop={this.onUserStop}
          onRecordStart={this.onRecordStart}
          onRecordStop={this.onRecordStop}
          onVideoFile={this.onVideoFile}
          onAbort={this.onAbort}
        />
        <div className='McqHeadingMain'>Live Interview</div>

        <div className='McqQuestionArea'>
          <div className='QuestionNumber'>Q.{current}</div>
          <div className='QuestionArea'>
            <div
              className='QuestionText'
              dangerouslySetInnerHTML={{
                __html: currentData && currentData.text,
              }}
            ></div>
            <div
              className='QuestionOptions'
              style={{
                display: width > 360 ? "flex" : "block",
                flexWrap: "wrap",
              }}
            >
              {options &&
                options.length > 0 &&
                options.map((item) => (
                  <div
                    className='OptionArea'
                    style={{
                      width: width < 360 ? `${width}px` : "360px",
                    }}
                  >
                    <Checkbox
                      className='checkbox'
                      checked={item.value}
                      onChange={(e) => this.changeOption(e, item.label)}
                      label={item.label}
                    />
                    <span className='textLabel'>{item.label}</span>
                  </div>
                ))}
            </div>
          </div>
          <div className='VideoArea'>
            <div className='Timer'>
              {duration ? (
                <TimerComponent
                  durationTime={duration}
                  className='timer-new'
                  isPlaying={isPlaying}
                  disableBtn={restartTimer}
                  nextBtn={true}
                  //   handleTimer={() => null}
                  handleTimer={this.handleTimer}
                  onTick={(time, remainingTime) => {
                    this.handleTimeUpdate(time, remainingTime);
                  }}
                />
              ) : null}
            </div>
            <div className='VideoRecord'>
              {" "}
              <WebcamComponent
                handleStartCaptureClick={this.handleStartCaptureClick}
                // handleAbort={this.handleAbort}
                handleDataAvailable={this.handleDataAvailable}
                handleStopCaptureClick={this.handleStopCaptureClick}
                capturing={this.state.capturing}
                webcamRef={this.webcamRef}
                recordedChunks={this.state.recordedChunks}
                handleDownload={this.handleDownload}
              />
              <div className='audioDiv'>
                <div className='audio-img'>
                  <img src={audio} />{" "}
                </div>
                {this.state.audioNew ? (
                  <div className='audio-analyser'>
                    <AudioAnalyser
                      amplitude={() => null}
                      audio={this.state.audioNew}
                    />
                  </div>
                ) : null}
              </div>
            </div>
            <div className='NextBtn'>
              <Button
                disabled={disablebtn}
                variant='contained'
                width={ this.state.skipableArrived ? '200px' : '350px'}
                className={`Button-next ${disablebtn ? "disabled" : "normal"}`}
                onClick={this.handleTimer}
              >
                {
                             this.state.skipableArrived  ? <span>Next Question</span>
                             :  <span>Next Question{' '}{`in ${this.state.remainingTime} seconds`}</span>
                           }
                <span>
                  <ArrowForwardIcon style={{ height: "15px", width: "15px" }} />
                </span>
              </Button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
const mapStateToProps = (state) => ({
  UserReducer: state.UserReducer,
});

function mapDispatchToProps(dispatch) {
  return {
    UserAction: bindActionCreators(Actions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(McqQuestions);
