import React, { Component } from "react";
import { connect } from "react-redux";
import Button from "@material-ui/core/Button";
import "../../styles/interview.css";
import { withRouter } from "react-router-dom";
import { bindActionCreators } from "redux";
import * as Actions from "../../store/User/UserAction";
import { TimerComponent } from "./timerComponent";
import Typography from "@material-ui/core/Typography";
import ReplayIcon from "@material-ui/icons/Replay";
import AudioAnalyser from "../Preview/AudioAnalyser";
import audio from "../../assets/images/audio.png";
import Dialog from "@material-ui/core/Dialog";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import ReadQuestion from "./ReadQuestion";
import WebcamComponent from "./WebcamComponent";
import ScreenRecording from "../Test/screenRecording";

let verbalSkipableValue = parseInt(process.env.REACT_APP_VERBAL_SKIP_TIME)
  ? parseInt(process.env.REACT_APP_VERBAL_SKIP_TIME)
  : 10;

let readQuestionTime = parseInt(process.env.REACT_APP_READ_QUESTION)
  ? parseInt(process.env.REACT_APP_READ_QUESTION)
  : 10;

let remainingGlobal = 0;
let Interval = "",
  scheduleIdGlobal = "",
  candidateIdGlobal = "",
  timedate = "",
  activeStep = 0,
  ScreenFileGlobal = "",
  ScreenFileNameGlobal = "";

export class Interview extends Component {
  constructor(props) {
    super(props);
    this.clockRef = React.createRef();
    this.mediaRecorderRef = React.createRef();
    this.timer = 0;
    this.state = {
      restartTimer: false,
      audioNew: null,
      ques: [],
      capturing: false,
      activeStep: 0,
      audio: false,
      video: false,
      recordedChunks: [],
      question: [],
      questions: [],
      videofilename: "",
      isDataArrived: true,
      questionData: [],
      submitClicked: false,
      faceNotDetected: 0,
      submittedQuestions: [],
      totalQuestions: 0,
      submissionQueue: [],
      responseId: "",
      noOfRetakes: 0,
      showDialog: false,
      showReadQuestion: false,
      totalRetakes: 0,
      retakesAvailable: 0,
      retake: false,
      showRetakeDialog: false,
      remainingTime: 0,
      isPlaying: true,
      skipable: false,
      ScheduleId: "",
      CandidateId: "",
      timerInput: 0,
      time: "",
      events: [],
      skipableArrived: false,
      // ScreenFile:'',
      // ScreenFileName:''
    };
    this.vid = React.createRef();
    this.audio = React.createRef();
    this.webcamRef = React.createRef();
    this.childRef = React.createRef();
  }

  async componentDidMount() {
    if (this.props && this.props.data) {
      const { id, candidateId, scheduleId } =
        this.props &&
        this.props.data &&
        this.props.data.data &&
        this.props.data.data.response;
      await this.setState({
        responseId: id,
        ScheduleId: scheduleId,
        CandidateId: candidateId,
      });

      scheduleIdGlobal = scheduleId;
      candidateIdGlobal = candidateId;
      let questionData = [];
      this.props.data &&
        this.props.data.verbalQuestions &&
        this.props.data.verbalQuestions.map((item) => {
          questionData.push({
            id: item.id,
            QuestionText: item.text,
            Questionstime: item.suggestedDuration,
          });
        });
      this.setState({
        questionData: questionData,
        totalQuestions: questionData.length,
        showReadQuestion: true,
      });

      let timerInput = 0;
      if (questionData.length > 0) {
        const val = questionData[this.state.activeStep].Questionstime;
        timerInput = Number(val);
        await this.setState({
          timerInput: timerInput,
        });
      }

      this.props &&
        this.props.data &&
        this.props.data.data &&
        this.props.data.data.response &&
        this.props.data.data.response.questionType &&
        this.props.data.data.response.questionType.length > 0 &&
        this.props.data.data.response.questionType.map((item) => {
          if (item.name.toLowerCase() == "verbal") {
            if (item.retake) {
              this.setState({
                totalRetakes: item.retake,
                retakesAvailable: item.retake - 1,
              });
            }
            if (item.skipable && this.state.timerInput) {
              this.setState({
                skipableArrived: true,
              });
              let time = (verbalSkipableValue + readQuestionTime) * 1000;
              setTimeout(() => {
                this.setState({
                  skipable: true,
                });
              }, time);
            }
          }
        });
      document.addEventListener("contextmenu", (e) => {
        e.preventDefault();
        const { events } = this.state;
        const data = {
          type: "browser",
          level: 2,
          metadata: "Right click triggered",
          timestamp: new Date().toISOString(),
        };
        events.push({
          ...data,
          time: this.state.timerInput - this.state.remainingTime,
        });
        id && this.setState({ events: events });
        id && this.addEventProctoring(id, data);
      });
      document.addEventListener("visibilitychange", (event) => {
        if (window.location.pathname.includes("/interview-process")) {
          if (document.visibilityState == "visible") {
            const { events } = this.state;
            const data = {
              type: "browser",
              level: 2,
              metadata: "tab is active",
              timestamp: new Date().toISOString(),
            };
            events.push({
              ...data,
              time: this.state.timerInput - this.state.remainingTime,
            });
            id && this.setState({ events: events });
            id && this.addEventProctoring(id, data);
          } else {
            const { events } = this.state;
            const data = {
              type: "browser",
              level: 2,
              metadata: "tab is inactive",
              timestamp: new Date().toISOString(),
            };
            events.push({
              ...data,
              time: this.state.timerInput - this.state.remainingTime,
            });
            id && this.setState({ events: events });
            id && this.addEventProctoring(id, data);
          }
        }
      });
    }
    this.getMicrophone();

    var today = new Date();
    var date =
      today.getFullYear() +
      "-" +
      (today.getMonth() + 1) +
      "-" +
      today.getDate();

    var time =
      today.getHours() + "-" + today.getMinutes() + "-" + today.getSeconds();

    var dateTime = date + "-" + time;
    this.setState({
      timedate: dateTime,
      // disble_next_button: true,
    });
    timedate = dateTime;
  }

  selectionOperation = (type) => {
    const { responseId } = this.state;
    const { events } = this.state;
    const data = {
      type: "browser",
      level: 2,
      metadata: `${type} action triggered`,
      timestamp: new Date().toISOString(),
    };
    events.push({
      ...data,
      time: this.state.timerInput - this.state.remainingTime,
    });
    responseId && this.setState({ events: events });
    responseId && this.addEventProctoring(responseId, data);
  };

  addEventProctoring = (id, data) => {
    this.props.UserAction.addEvent({
      responseId: id,
      event: data,
    });
  };

  async getMicrophone() {
    const audio = await navigator.mediaDevices.getUserMedia({
      audio: true,
    });
    this.setState({ audioNew: audio });
  }
  static getDerivedStateFromProps(nextProps, currentState) {
    if (
      nextProps &&
      nextProps.UserReducer &&
      nextProps.UserReducer.isScreenShotDataArrived &&
      nextProps.UserReducer.screenShot &&
      nextProps.UserReducer.screenShot.data
    ) {
      nextProps.UserReducer.isScreenShotDataArrived = false;
      if (!nextProps.UserReducer.screenShot.data.faces) {
        currentState.faceNotDetected = currentState.faceNotDetected + 1;
      }
    }

    return currentState;
  }

  // isStepSkipped = (step) => {
  //   return this.state.skipped.has(step);
  // };

  handleStart = () => {
    this.clockRef.current.start();
  };

  handleTimeUpdate = (time, remainingTime) => {
    if (this.state.time != time) {
      this.setState({ time: time, remainingTime: remainingTime });
    }
  };
  handleNext = async (questiondata) => {
    if (!this.state.retake) {
      await this.setState({ restartTimer: true, isPlaying: true });
      await this.handleDownload(questiondata);
      let quesdata = [];

      questiondata.map((item) => {
        quesdata.push(item.id);
      });

      this.getStepContent(questiondata, this.state.activeStep);

      await this.setState({
        activeStep: this.state.activeStep + 1,
        showReadQuestion: true,
        retakesAvailable: this.state.totalRetakes - 1,
      });
      activeStep = activeStep + 1;
      let timerInput = 0;
      if (this.state.questionData.length > 0) {
        const val =
          this.state.questionData[this.state.activeStep].Questionstime;
        timerInput = Number(val);
        await this.setState({
          timerInput: timerInput,
        });
      }
    }
  };

  getStepContent(questiondata, stepIndex) {
    if (questiondata.length > 0) {
      let steparray = [];
      questiondata.map((item) => {
        steparray.push(item.QuestionText);
      });
      const value = steparray[this.state.activeStep];
      switch (this.state.activeStep) {
        case this.state.activeStep:
          return (
            <div>
              <div className='question-text'>
                Q{this.state.activeStep + 1}.
                <div
                  dangerouslySetInnerHTML={{
                    __html: value,
                  }}
                ></div>
              </div>
            </div>
          );
        default:
          return "Unknown stepIndex";
      }
    }
  }

  handleFinish = async (questiondata) => {
    if (!this.state.retake) {
      this.setState({
        submitClicked: true,
        // isPlaying: true,
      });
      await this.handleDownload(questiondata);
      let quesdata = [];

      questiondata.map((item) => {
        quesdata.push(item.id);
      });
      const question = [];
      question.push(this.getStepContent(this.state.activeStep));

      this.props.submitClickedHandler();
    }
  };

  handlePriorUpload = async (file, name, quesdata) => {
    if (this.state.skipable && this.state.timerInput) {
      this.setState({
        skipable: false,
      });
      let time = (verbalSkipableValue + readQuestionTime) * 1000;

      setTimeout(() => {
        this.setState({
          skipable: true,
        });
      }, time);
    }
    var formdata = new FormData();
    formdata.append("video", file, name);
    if (ScreenFileGlobal) {
      formdata.append("video", ScreenFileGlobal, ScreenFileNameGlobal);
    }
    formdata.append("timeTaken", remainingGlobal);
    formdata.append("events", JSON.stringify(this.state.events));
    const payload = {
      questionId: quesdata[this.state.activeStep],
      responseId: this.state.responseId,
      type: "verbal",
      questionStatus: 3,
      timeTaken: remainingGlobal,
      // videoFile: file,
      // videoFileName: name,
      // screenFile : ScreenFileGlobal,
      // screenFileName : ScreenFileNameGlobal
    };
    let quesNo = this.state.activeStep + 1;
    let questionType = "verbal";
    let indexDbData = {
      questionId: quesdata[this.state.activeStep],
      responseId: this.state.responseId,
      type: "verbal",
      questionStatus: 3,
      timeTaken: remainingGlobal,
      videoFile: file,
      videoFileName: name,
      screenFile: ScreenFileGlobal,
      screenFileName: ScreenFileNameGlobal,
      events: JSON.stringify(this.state.events),
    };
    this.props.UserAction.addEvent({
      responseId: this.state.responseId,
      event: {
        type: "debug",
        level: 3,
        metadata: {
          type: "Question Submit Start",
          questionId: quesdata[this.state.activeStep],
          responseId: this.state.responseId,
          type: "verbal",
          questionStatus: 3,
          events: this.state.events,
          timeTaken: remainingGlobal,
        },
        timestamp: new Date().toISOString(),
      },
      questionId: quesdata[this.state.activeStep],
      type: "verbal",
      questionStatus: 3,
    });
    // remainingGlobal = 0;
    // clearInterval(Interval);

    this.props.upload(formdata, payload, indexDbData, quesNo, questionType);

    this.setState({
      faceNotDetected: 0,
    });
  };

  handleStartCaptureClick = () => {
    let isSafari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent);

    if (this.mediaRecorderRef) {
      this.mediaRecorderRef.current = new MediaRecorder(
        this.webcamRef.current.stream,
        {
          mimeType: isSafari
            ? 'video/mp4; codecs="avc1.42E01E, mp4a.40.2"'
            : "video/webm",
          audioBitsPerSecond: 128000,
          videoBitsPerSecond: 625000,
        }
      );
    }
    this.setState(
      {
        capturing: true,
      },
      () => {
        this.childRef.current.startCapture();
      }
    );

    ScreenFileGlobal = "";
    ScreenFileNameGlobal = "";
    // setTimeout(()=>{
    //   this.childRef.current.startCapture()
    // },1000)
    // remainingGlobal = 0;

    // clearInterval(Interval);
    // Interval = setInterval(() => {
    //   remainingGlobal += 1;
    // }, 1000);

    if (
      this.mediaRecorderRef &&
      this.mediaRecorderRef.current &&
      this.mediaRecorderRef.current.state == "inactive"
    ) {
      this.mediaRecorderRef.current.start();
      this.mediaRecorderRef.current.addEventListener(
        "dataavailable",
        this.handleDataAvailable
      );
    }
  };

  handleAbort = () => {
    if (
      this.mediaRecorderRef &&
      this.mediaRecorderRef.current &&
      this.mediaRecorderRef.current.state == "recording"
    ) {
      this.childRef.current.abortCapture();
      this.mediaRecorderRef.current.stop();
      this.mediaRecorderRef.current.addEventListener("stop", () => {
        this.setState({
          recordedChunks: [],
        });
      });
      this.setState({
        capturing: false,
      });

      this.handleStartCaptureClick();
    }
  };

  handleStopCaptureClick = () => {
    if (
      this.mediaRecorderRef &&
      this.mediaRecorderRef.current &&
      this.mediaRecorderRef.current.state == "recording"
    ) {
      this.mediaRecorderRef.current.stop();
      this.setState({
        capturing: false,
      });
    }
  };

  handleDownload = (questiondata) => {
    let quesdata = [];

    questiondata.map((item) => {
      quesdata.push(item.id);
    });
    //  let IntervalScreen = setInterval(() => {
    if (this.state.recordedChunks.length) {
      const blob = new Blob(this.state.recordedChunks, {
        type: "video/webm",
      });
      const url = URL.createObjectURL(blob);
      const a = document.createElement("a");
      document.body.appendChild(a);
      a.style = "display: none";
      a.href = url;
      window.URL.revokeObjectURL(url);
      this.setState({ recordedChunks: [] });
      var file = new File(
        [blob],
        `${this.state.ScheduleId}-${this.state.CandidateId}-${this.state.timedate}-${this.state.activeStep}.mp4`
      );
      var filedata = `${this.state.ScheduleId}-${this.state.CandidateId}-${this.state.timedate}-${this.state.activeStep}.mp4`;
      this.setState({ videofilename: filedata });
      this.handlePriorUpload(
        file,
        `${this.state.ScheduleId}-${this.state.CandidateId}-${this.state.timedate}-${this.state.activeStep}.mp4`,
        quesdata
      );
      // clearInterval(IntervalScreen)
    }
    // }, 1000);
  };

  handleDataAvailable = async ({ data }) => {
    if (data.size > 0) {
      await this.setState({
        recordedChunks: this.state.recordedChunks.concat(data),
      });
      remainingGlobal = this.state.timerInput - this.state.remainingTime;

      // setTimeout(()=>{
      if (this.state.activeStep == this.state.questionData.length - 1) {
        this.handleFinish(this.state.questionData);
      } else {
        this.handleNext(this.state.questionData);
      }
      // },2000)
    }
  };

  handleTimer = async (questionData) => {
    await this.setState({
      restartTimer: false,
      showDialog: false,
      retake: false,
      isPlaying: false,
    });
    setTimeout(() => {
      this.handleStopCaptureClick(questionData);
    }, 2000);
    this.childRef.current.stopCapture();
  };

  capture = () => {
    const imgSrc =
      this.webcamRef &&
      this.webcamRef.current &&
      this.webcamRef.current.getScreenshot();
    let image =
      imgSrc && imgSrc.substring("data:image/jpeg;base64".length + 1)
        ? imgSrc.substring("data:image/jpeg;base64".length + 1)
        : null;
    this.setState({
      screenShot: image,
    });

    this.props.UserAction.verifyScreenshot({
      base64: image,
    });
  };

  readQuestionTimerEnds = () => {
    this.setState({ showReadQuestion: false });
  };

  retakeHandler = async () => {
    if (this.state.retakesAvailable && this.state.retakesAvailable > 0) {
      await this.setState({
        retakesAvailable: this.state.retakesAvailable - 1,
        showRetakeDialog: false,
        restartTimer: true,
        retake: true,
        showDialog: false,
        events: [],
      });
      const payload = {
        questionId: this.state.questionData[this.state.activeStep].id,
        responseId: this.state.responseId,
        type: "verbal",
        questionStatus: 2,
      };
      this.props.UserAction.updateResponse(payload);
      this.handleAbort();
      this.setState({
        restartTimer: false,
      });
      if (
        this.state.skipable &&
        this.state.timerInput &&
        !this.state.showReadQuestion
      ) {
        this.setState({
          skipable: false,
        });
        let time = verbalSkipableValue * 1000;

        setTimeout(() => {
          this.setState({
            skipable: true,
          });
        }, time);
      }
    }
  };

  retakeClicked = () => {
    if (
      !(
        this.state.totalRetakes - this.state.retakesAvailable ===
        this.state.totalRetakes
      ) &&
      this.state.totalRetakes - this.state.retakesAvailable > 0
    ) {
      this.setState({ showRetakeDialog: true });
    }
  };

  handleEarlySubmit = () => {
    if (
      this.state.skipable &&
      this.state.retakesAvailable &&
      this.state.retakesAvailable > 0
    ) {
      this.setState({
        showDialog: true,
      });
    } else if (this.state.skipable) {
      this.handleTimer(this.state.questionData);
    }
  };

  handleTimerEnd = () => {
    if (this.state.retakesAvailable && this.state.retakesAvailable > 0) {
      this.setState({
        showDialog: true,
      });
    } else {
      this.handleTimer(this.state.questionData);
    }
  };

  onScreenAccess() {
    console.log("got Screen access");
  }
  onRecordStart() {
    console.log("started recording");
  }
  onRecordStop() {
    console.log("stopped recording");
  }
  onUserStop() {
    console.log("user stopped screen access");
    ScreenFileGlobal = "";
    ScreenFileNameGlobal = "";
  }
  async onVideoFile(blob) {
    let ScreenFileTemp = new File(
      [blob],
      `$${scheduleIdGlobal}-${candidateIdGlobal}-${timedate}-${activeStep}.mp4`
    );
    let ScreenFileNameTemp = `${scheduleIdGlobal}-${candidateIdGlobal}-${timedate}-${activeStep}Screen.mp4`;

    ScreenFileGlobal = ScreenFileTemp;
    ScreenFileNameGlobal = ScreenFileNameTemp;
  }
  onAbort() {
    console.log("recieved abort callback");
  }

  render() {
    let questiondata = this.state.questionData;
    localStorage.setItem("questiondata", JSON.stringify(questiondata));
    var xyz = JSON.parse(localStorage.getItem("questiondata"));
    let questiondata1 = [];
    let quesreadtime = [];
    xyz.map((item) => {
      questiondata1.push(parseInt(item.Questionstime));
      quesreadtime.push(10000);
    });

    // var n = questiondata1[this.state.act_num];
    // var r = quesreadtime[this.state.act_num];

    const steps = [];
    for (let i = 1; i <= questiondata.length; i++) {
      steps.push(i);
    }

    // const videoConstraints = {
    //   facingMode: { exact: "user" },
    // };

    return (
      <div
        onCopy={() => this.selectionOperation("Copy")}
        onCut={() => this.selectionOperation("Cut")}
        onPaste={() => this.selectionOperation("Paste")}
      >
        <ScreenRecording
          ref={this.childRef}
          onScreenAccess={this.onScreenAccess}
          onUserStop={this.onUserStop}
          onRecordStart={this.onRecordStart}
          onRecordStop={this.onRecordStop}
          onVideoFile={this.onVideoFile}
          onAbort={this.onAbort}
        />
        {localStorage.getItem("login") && localStorage.getItem("preview") ? (
          this.state.showReadQuestion ? (
            <ReadQuestion
              durationTime={readQuestionTime}
              question={this.state.questionData[this.state.activeStep]}
              quesNo={this.state.activeStep + 1}
              responseId={this.state.responseId}
              type='verbal'
              readQuestionTimerEnds={this.readQuestionTimerEnds}
            />
          ) : (
            <div className='interview-main-div'>
              <Dialog
                className='first-dialog-submit'
                open={this.state.showDialog}
                // onClose={() => {
                //   this.setState({
                //     showDialog: false,
                //   });
                // }}
                aria-labelledby='alert-dialog-title'
                aria-describedby='alert-dialog-description'
              >
                <div className='main-dialog-heading'>
                  Do you want to retake or submit?
                </div>
                <div className='main-dialog-sub-heading'>
                  You have {this.state.retakesAvailable} retakes left.
                </div>
                <div className='main-dialog-buttons-wrapper'>
                  <div
                    className='dialog-retry-button'
                    onClick={this.retakeHandler}
                  >
                    Retake
                  </div>
                  <div
                    className='dialog-submit-button'
                    onClick={this.handleTimer}
                  >
                    Submit
                  </div>
                </div>
              </Dialog>

              {this.state.retakesAvailable &&
              this.state.retakesAvailable > 0 ? (
                <Dialog
                  className='first-dialog-submit'
                  open={this.state.showRetakeDialog}
                  aria-labelledby='alert-dialog-title'
                  aria-describedby='alert-dialog-description'
                >
                  <div className='main-dialog-heading'>
                    Are you sure you want to retake?
                  </div>
                  <div className='main-dialog-sub-heading'>
                    You have {this.state.retakesAvailable} retakes left.
                  </div>
                  <div className='main-dialog-buttons-wrapper'>
                    <div
                      className='dialog-retry-button'
                      onClick={() => {
                        this.setState({ showRetakeDialog: false });
                      }}
                    >
                      No
                    </div>
                    <div
                      className='dialog-submit-button'
                      onClick={this.retakeHandler}
                    >
                      Yes
                    </div>
                  </div>
                </Dialog>
              ) : null}
              <div className='interview-row-wrapper'>
                <div className='Inter-1'>
                  {window.screen.width > 1024 ? (
                    <div className='previews-heading'>
                      <Typography>Live Interview</Typography>
                    </div>
                  ) : null}
                  {window.screen.width > 1024 ? (
                    <div className='previews-subHeading1'>
                      {/* <Typography>Please don't try to refer anything from any other means.</Typography> */}
                    </div>
                  ) : null}
                </div>
                <div className='inter-2 total-questions'>
                  <div>
                    <div className='questions-wrapper'>
                      <div className='total'> Total Questions</div>
                      <div className='circle-interview'>
                        {this.state.activeStep + 1}/{questiondata.length}
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              <div class='fluid-container'>
                <div className='interview-row-wrapper-new'>
                  <div className='left-interview'>
                    <div className='video-div'>
                      <div className='interview-video-new'>
                        <WebcamComponent
                          handleStartCaptureClick={this.handleStartCaptureClick}
                          handleAbort={this.handleAbort}
                          handleDataAvailable={this.handleDataAvailable}
                          handleStopCaptureClick={this.handleStopCaptureClick}
                          capturing={this.state.capturing}
                          webcamRef={this.webcamRef}
                          recordedChunks={this.state.recordedChunks}
                          handleDownload={this.handleDownload}
                        />

                        <div className='bottom-video-div'>
                          <div className='retake-button-div'>
                            <div
                              className={
                                !this.state.totalRetakes ||
                                this.state.totalRetakes -
                                  this.state.retakesAvailable ==
                                  this.state.totalRetakes
                                  ? "retake-disabled-button"
                                  : "retake-button"
                              }
                              onClick={this.retakeClicked}
                            >
                              <span>
                                <ReplayIcon
                                  style={{
                                    height: "20px",
                                    width: "20px",
                                    margin: "0 5px",
                                  }}
                                />
                              </span>
                              Retake{" "}
                              {this.state.totalRetakes ? (
                                <span>
                                  {" "}
                                  {this.state.totalRetakes -
                                    this.state.retakesAvailable}{" "}
                                  / {this.state.totalRetakes}
                                </span>
                              ) : (
                                ""
                              )}
                            </div>
                          </div>
                          <div className='timer-button-div'>
                            <div className='timer-button'>
                              <TimerComponent
                                // nextBtn={this.state.disble_next_button}
                                durationTime={this.state.timerInput}
                                // disableBtn={this.state.restartTimer}
                                // remainingTimeHandler={this.remainingTimeHandler}
                                handleTimer={() => {
                                  // this.handleTimer(questiondata)
                                  console.log("handle");
                                }}
                                isPlaying={true}
                                className='timer-2'
                                onTick={(time, remainingTime) => {
                                  this.handleTimeUpdate(time, remainingTime);
                                }}
                              />
                              {this.state.time}
                            </div>
                          </div>
                          <div className='audio-analyser-div'>
                            <div className='audio-img'>
                              {" "}
                              <img src={audio} />{" "}
                            </div>
                            {this.state.audioNew ? (
                              <div>
                                {" "}
                                <AudioAnalyser
                                  amplitude={() => null}
                                  audio={this.state.audioNew}
                                />{" "}
                              </div>
                            ) : null}
                          </div>
                          <div className='submit-button-div'>
                            <div
                              className={
                                this.state.skipable
                                  ? "submit-button"
                                  : "submit-button-disabled"
                              }
                              onClick={this.handleEarlySubmit}
                            >
                              Submit
                            </div>
                          </div>
                        </div>

                        <div className='total-questions-new'>
                          <div>
                            <div className='questions-wrapper'>
                              <div className='total'>Total Questions</div>
                              <div className='circle-interview'>
                                {this.state.activeStep + 1}/
                                {questiondata.length}
                              </div>
                            </div>
                          </div>
                          {this.state.timerInput !== 0 &&
                            window.screen.width <= 1024 && (
                              <TimerComponent
                                durationTime={this.state.timerInput}
                                disableBtn={this.state.restartTimer}
                                remainingTimeHandler={this.remainingTimeHandler}
                                handleTimer={() => {
                                  this.handleTimerEnd();
                                }}
                                isPlaying={this.state.isPlaying}
                                className='timer-2'
                                onTick={(time, remainingTime) => {
                                  this.handleTimeUpdate(time, remainingTime);
                                }}
                              />
                            )}
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className='right-interview'>
                    <div className='question-div'>
                      {this.state.timerInput !== 0 &&
                        window.screen.width > 1024 && (
                          <TimerComponent
                            // nextBtn={this.state.disble_next_button}
                            durationTime={this.state.timerInput}
                            remainingTimeHandler={this.remainingTimeHandler}
                            disableBtn={this.state.restartTimer}
                            handleTimer={() => {
                              this.handleTimerEnd();
                            }}
                            isPlaying={this.state.isPlaying}
                            className='timer-1'
                          />
                        )}

                      <div className='content-div'>
                        <div className='qustion-content-div'>
                          {this.state.activeStep === steps.length ? (
                            <div></div>
                          ) : (
                            <div>
                              <span
                                className='laptop-only'
                                style={{
                                  display: "flex",
                                  fontSize: "12px",
                                  marginLeft: "20px",
                                }}
                              >
                                Please Answer
                              </span>
                              <div className='question'>
                                <div>
                                  {this.getStepContent(
                                    questiondata,
                                    this.state.activeStep
                                  )}
                                </div>
                              </div>
                            </div>
                          )}
                        </div>

                        <div className='buttons-wrapper'>
                          <Button
                            // disabled={this.state.disable_submit_btn}
                            variant='contained'
                            className={
                              this.state.skipable
                                ? "Button-next"
                                : "Button-next-disabled"
                            }
                            style={{
                              width: this.state.skipableArrived
                                ? "200px"
                                : "350px",
                            }}
                            onClick={this.handleEarlySubmit}
                          >
                            {this.state.skipableArrived ? (
                              <span>Next Question</span>
                            ) : (
                              <span>
                                Next Question{" "}
                                {`in ${this.state.remainingTime} seconds`}
                              </span>
                            )}
                            <span>
                              <ArrowForwardIcon
                                style={{ height: "15px", width: "15px" }}
                              />
                            </span>
                          </Button>

                          <Button
                            // disabled={this.state.disable_submit_btn}
                            variant='contained'
                            className='Button-retry'
                            onClick={this.retakeClicked}
                          >
                            <span>Retake</span>
                            <span>
                              <ReplayIcon
                                style={{ height: "15px", width: "15px" }}
                              />
                            </span>
                          </Button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div
                style={{
                  display: "flex",
                  justifyContent: "end",
                  marginRight: "30px",
                  marginTop: "20px",
                }}
                className='interview-logo'
              >
                {/* <img style={{maxWidth:'100px'}} src={birballogo}></img> */}
              </div>
            </div>
          )
        ) : (
          <div>Access Blocked</div>
        )}
      </div>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    // CandidateReducer: state.CandidateReducer
    UserReducer: state.UserReducer,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    // candidateActions: bindActionCreators(Actions, dispatch),
    UserAction: bindActionCreators(Actions, dispatch),
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(Interview));
